
'use client';

import * as React from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import InteractiveTreeExplorer from '@/components/dashboard/interactive-tree-explorer';
import ParameterPlaygroundSection from '@/components/dashboard/parameter-playground-section';
import { useDashboardState } from '@/components/dashboard/dashboard-state-provider';
import { AUDIENCE_DATA } from '@/lib/data';

export default function Stage3Page() {
    const { audience, parameters, setParameters } = useDashboardState();
    const audienceData = AUDIENCE_DATA[audience];

    return (
        <Card className="shadow-lg">
            <CardHeader>
                <CardTitle className="font-headline text-2xl">Stage 3: Forest Formation</CardTitle>
                <CardDescription>{audienceData.metaphors.forest}</CardDescription>
            </CardHeader>
            <CardContent>
                 <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div className="flex flex-col gap-4">
                        <CardHeader className="p-0">
                            <CardTitle>The Forest</CardTitle>
                            <CardDescription>Multiple trees are built to form a "forest". Click a tree to explore it.</CardDescription>
                        </CardHeader>
                        <div className="p-6 border rounded-md">
                            <div className="grid grid-cols-5 gap-4">
                                {Array.from({ length: parameters.n_estimators }).map((_, i) => (
                                    <InteractiveTreeExplorer key={i} treeId={i+1} audienceData={audienceData} />
                                ))}
                            </div>
                        </div>
                    </div>
                    <div>
                        <ParameterPlaygroundSection 
                            relevantParams={['n_estimators']} 
                            audienceData={audienceData} 
                            parameters={parameters} 
                            setParameters={setParameters} 
                        />
                    </div>
                </div>
            </CardContent>
        </Card>
    );
}
